            </div>
          </el-col>
          <el-col :span="6">
            <div class="info-item">
              <label>Êï∞ÊçÆÁÇπÊï∞:</label>
              <span>{{ analysisResult.monitoring_summary.cpu_data_points }}‰∏™</span>
            </div>
          </el-col>
          <el-col :span="6">
