      </el-card>

      <!-- ÊÄßËÉΩÂõæË°® -->
      <el-card class="charts-card">
        <div slot="header">
          <span>üìà ÊÄßËÉΩË∂ãÂäøÂõæË°®</span>
        </div>
        
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="chart-container">
              <h4>CPU‰ΩøÁî®ÁéáË∂ãÂäø</h4>
              <div ref="cpuChart" class="chart"></div>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="chart-container">
              <h4>ÂÜÖÂ≠ò‰ΩøÁî®ÁéáË∂ãÂäø</h4>
              <div ref="memoryChart" class="chart"></div>
            </div>
          </el-col>
        </el-row>
        
        <el-row :gutter="20" style="margin-top: 20px;">
