<script>
import * as echarts from 'echarts'
import { analyzeMonitoringData } from '@/api/performance'

export default {
  name: 'MonitorAnalysis',
  data() {
    return {
      loading: false,
      analysisResult: null,
      charts: {
        cpu: null,
        memory: null,
        load: null,
        core: null
      }
    }
  },
  methods: {
    async handleFileUpload(file) {
      if (file.size > 10 * 1024 * 1024) {
        this.$message.error('Êñá‰ª∂Â§ßÂ∞è‰∏çËÉΩË∂ÖËøá10MB')
        return false
      }

      this.loading = true
      
      try {
        const fileContent = await this.readFileAsText(file)
        const monitoringData = JSON.parse(fileContent)
        
        // Ë∞ÉÁî®ÂàÜÊûêAPI
        const response = await analyzeMonitoringData(monitoringData)
        
        if (response.success) {
          this.analysisResult = response.analysis
          this.$nextTick(() => {
            this.initCharts()
          })
          this.$message.success('ÁõëÊéßÊï∞ÊçÆÂàÜÊûêÂÆåÊàê')
        } else {
          this.$message.error(response.error || 'ÂàÜÊûêÂ§±Ë¥•')
        }
      } catch (error) {
        console.error('ÂàÜÊûêÈîôËØØ:', error)
        if (error instanceof SyntaxError) {
          this.$message.error('JSONÊñá‰ª∂Ê†ºÂºèÈîôËØØ')
        } else {
          this.$message.error('ÂàÜÊûêËøáÁ®ã‰∏≠ÂèëÁîüÈîôËØØ: ' + error.message)
        }
      } finally {
        this.loading = false
      }
      
      return false // ÈòªÊ≠¢ÈªòËÆ§‰∏ä‰º†Ë°å‰∏∫
    },

    readFileAsText(file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.onload = e => resolve(e.target.result)
        reader.onerror = reject
        reader.readAsText(file)
      })
    },

    resetAnalysis() {
      this.analysisResult = null
      this.destroyCharts()
    },

    getScoreClass(score) {
      if (score >= 85) return 'score-excellent'
      if (score >= 70) return 'score-good'
      return 'score-poor'
    },

    getScoreText(score) {
      if (score >= 85) return '‰ºòÁßÄ'
      if (score >= 70) return 'ËâØÂ•Ω'
      return 'ÈúÄË¶Å‰ºòÂåñ'
    },

    getScoreDescription(score) {
      if (score >= 85) return 'Á≥ªÁªüÊÄßËÉΩË°®Áé∞‰ºòÁßÄÔºåËøêË°åÁ®≥ÂÆö'
      if (score >= 70) return 'Á≥ªÁªüÊÄßËÉΩËâØÂ•ΩÔºåÊúâÂ∞ëÈáè‰ºòÂåñÁ©∫Èó¥'
      return 'Á≥ªÁªüÂ≠òÂú®ÊÄßËÉΩÈóÆÈ¢òÔºåÂª∫ËÆÆÁ´ãÂç≥‰ºòÂåñ'
    },

    getBottleneckIcon(severity) {
      const icons = {
        critical: 'üî¥',
        high: 'üü†',
        medium: 'üü°',
        low: 'üîµ'
      }
      return icons[severity] || 'üîµ'
    },

    getTagType(severity) {
      const types = {
        critical: 'danger',
        high: 'warning',
        medium: 'info',
        low: 'success'
      }
      return types[severity] || 'info'
    },

    getSeverityText(severity) {
      const texts = {
        critical: '‰∏•Èáç',
        high: 'È´ò',
        medium: '‰∏≠',
        low: '‰Ωé'
      }
      return texts[severity] || 'Êú™Áü•'
    },

    initCharts() {
      this.initCpuChart()
      this.initMemoryChart()
      this.initLoadChart()
      this.initCoreChart()
    },

    initCpuChart() {
      if (!this.$refs.cpuChart) return
      
      this.charts.cpu = echarts.init(this.$refs.cpuChart)
      const data = this.analysisResult.charts_data.cpu_timeline
      
      const option = {
        tooltip: {
          trigger: 'axis',
          formatter: '{b}<br/>CPU‰ΩøÁî®Áéá: {c}%'
        },
        xAxis: {
          type: 'category',
          data: data.map(item => new Date(item.time).toLocaleTimeString())
        },
        yAxis: {
          type: 'value',
          min: 0,
          max: 100,
          axisLabel: {
            formatter: '{value}%'
          }
        },
        series: [{
          data: data.map(item => item.cpu_percent),
          type: 'line',
          smooth: true,
          itemStyle: {
            color: '#409EFF'
          },
          areaStyle: {
            opacity: 0.3
          }
        }]
      }
      
      this.charts.cpu.setOption(option)
    },

    initMemoryChart() {
      if (!this.$refs.memoryChart) return
      
      this.charts.memory = echarts.init(this.$refs.memoryChart)
      const data = this.analysisResult.charts_data.memory_timeline
      
      const option = {
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['ÂÜÖÂ≠ò‰ΩøÁî®Áéá', '‰∫§Êç¢ÂàÜÂå∫‰ΩøÁî®Áéá']
        },
        xAxis: {
          type: 'category',
          data: data.map(item => new Date(item.time).toLocaleTimeString())
        },
        yAxis: {
          type: 'value',
          min: 0,
          max: 100,
          axisLabel: {
            formatter: '{value}%'
          }
        },
        series: [
          {
            name: 'ÂÜÖÂ≠ò‰ΩøÁî®Áéá',
            data: data.map(item => item.memory_percent),
            type: 'line',
            smooth: true,
            itemStyle: {
              color: '#67C23A'
            }
          },
          {
            name: '‰∫§Êç¢ÂàÜÂå∫‰ΩøÁî®Áéá',
            data: data.map(item => item.swap_percent),
            type: 'line',
            smooth: true,
            itemStyle: {
              color: '#F56C6C'
            }
          }
        ]
      }
      
      this.charts.memory.setOption(option)
    },

    initLoadChart() {
      if (!this.$refs.loadChart) return
      
      this.charts.load = echarts.init(this.$refs.loadChart)
      const data = this.analysisResult.charts_data.cpu_timeline
      
      const option = {
        tooltip: {
          trigger: 'axis',
          formatter: '{b}<br/>Ë¥üËΩΩ: {c}'
        },
        xAxis: {
          type: 'category',
          data: data.map(item => new Date(item.time).toLocaleTimeString())
        },
        yAxis: {
          type: 'value',
          min: 0
        },
        series: [{
          data: data.map(item => item.load_1min),
          type: 'line',
          smooth: true,
          itemStyle: {
            color: '#E6A23C'
          },
          areaStyle: {
            opacity: 0.3
          }
        }]
      }
      
      this.charts.load.setOption(option)
    },

    initCoreChart() {
      if (!this.$refs.coreChart) return
      
      this.charts.core = echarts.init(this.$refs.coreChart)
      const data = this.analysisResult.charts_data.cpu_cores
      
      const option = {
        tooltip: {
          trigger: 'axis',
          formatter: '{b}<br/>‰ΩøÁî®Áéá: {c}%'
        },
        xAxis: {
          type: 'category',
          data: data.map(item => item.core)
        },
        yAxis: {
          type: 'value',
          min: 0,
          max: 100,
          axisLabel: {
            formatter: '{value}%'
          }
        },
        series: [{
          data: data.map(item => ({
            value: item.usage,
            itemStyle: {
              color: item.usage > 80 ? '#F56C6C' : 
                     item.usage > 60 ? '#E6A23C' : '#67C23A'
            }
          })),
          type: 'bar'
        }]
      }
      
      this.charts.core.setOption(option)
    },

    destroyCharts() {
      Object.values(this.charts).forEach(chart => {
        if (chart) {
          chart.dispose()
        }
      })
      this.charts = {
        cpu: null,
        memory: null,
        load: null,
        core: null
      }
    }
  },

  beforeDestroy() {
    this.destroyCharts()
  }
}
</script>

