  margin-right: 15px;
  flex-shrink: 0;
}

.recommendation-content {
  color: #2c3e50;
  line-height: 1.6;
}

.no-issues-card {
  margin-bottom: 30px;
}

.no-issues-content {
  text-align: center;
  padding: 40px;
}

.no-issues-content h3 {
  margin: 20px 0 10px;
  color: #67C23A;
}

.no-issues-content p {
  color: #666;
}

@media (max-width: 768px) {
  .score-display {
    flex-direction: column;
    text-align: center;
  }

  .score-circle {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .chart {
    height: 250px;
  }
}
</style>



